.root {
  display: grid;
  grid-template-columns: 244px 1fr;
  grid-template-rows: 72px 1fr;
  grid-gap: 8px;
  padding: 16px 24px;
  max-width: 1380px;
}

.root iframe {
  height: 100vh;
  width: 1500px;
  position: absolute;
  top: 8px;
  left: 4px;
  border: 0;
  z-index: -1;
}

.header {
  grid-row: 1 / -1;
  height: 100%;
}
