.root {
  border-radius: var(--grid-baseline);
  display: grid;
  padding: calc(var(--grid-baseline) * 2);
  padding-bottom: calc(var(--grid-baseline) * 3);
  grid-gap: 32px;
  align-items: center;

  color: var(--mono-white);
  font-family: "Canva Sans", sans-serif;

  max-width: 70vw;
}

.heading {
  font-weight: 700;
  font-size: 32px;
  text-align: center;
  font-variation-settings: "opsz" 0;
  text-shadow: 0 0 1px rgb(68 92 116 / 2%), 0 2px 8px rgb(57 76 96 / 15%);
  line-height: 1.3;
  color: var(--mono-white-a50);
}

.workSuite {
  display: grid;
  grid-auto-flow: column;
  grid-gap: calc(var(--grid-baseline) * 6);
  grid-auto-columns: max-content;
  justify-content: center;
  justify-items: center;

  grid-auto-columns: 1fr;
  max-width: min-content;
  justify-self: center;

  grid-row: 3;
  grid-column: 1;
}

.workSuiteOption {
  display: grid;
  grid-gap: var(--grid-baseline);
  font-size: 12px;
  justify-content: center;
  font-weight: 500;
  white-space: pre;
}

.workSuiteOption svg {
  border-radius: 100%;
  background: var(--mono-white-a500);
  padding: var(--grid-baseline);
  width: calc(var(--grid-baseline) * 6);
  height: calc(var(--grid-baseline) * 6);
  margin: 0 auto;
  transition: 0.15s;
  background-color: var(--color);
  fill: var(--mono-white);
  box-shadow: inset 0 0 0 1px hsla(0, 0%, 100%, 0.1),
    0 2px 8px rgba(13, 18, 22, 0.15);
}

.workSuiteOption:not(:hover):not(.active) svg {
  /* fill: var(--mono-white); */
  fill: var(--color);
  background-color: var(--mono-white);
}

.workSuiteOption:not(:hover):not(.active) svg {
  /* fill: var(--mono-white); */
}

.workSuiteListItem:last-of-type:hover svg {
  background-color: var(--mono-white-a400);
}

.workSuiteOption:hover svg,
.active svg {
  transform: scale(1.1);
}

.workSuiteOption {
  position: relative;
}

.workSuiteOption svg {
  overflow: visible;
}

.root {
  position: relative;
}

[id="triangle"] {
  background: white;
  width: 16px;
  height: 16px;
  position: absolute;
  bottom: -9px;
  transform: rotate(45deg);
  border-radius: 4px;
  left: 124px;
  transition: 0.3s;
  opacity: 0;
}

.moveTriangle {
  transform: translateX(446px) rotate(45deg);
}

.input {
  border-radius: 4px;
  width: 600px;
  margin: 0 auto;
  background: white;
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: min-content 1fr;
  padding: 8px;
  color: black;
  grid-row: 2;
  grid-column: 1;
  z-index: 1;
}

.input input {
  width: 100%;
  border: none;
}

.header {
  display: grid;
  grid-auto-flow: column;
  justify-content: end;
  position: relative;
  grid-gap: 8px;
  grid-row: 1;
  grid-column: 1;
  z-index: 1;
}

.heading {
  position: absolute;
  width: 100%;
}

.headerButton {
  height: 36px;
  background-color: var(--mono-white-a500);
  font-size: 0.875rem;
  padding: 0 16px;
  border-radius: 4px;
  font-weight: 500;
}

.background {
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
}

.background::before {
  content: "";
  background: linear-gradient(
    119.42deg,
    #cc00c4 8.94%,
    #7d2ae8 54.02%,
    #00c4cc 95.49%
  );
  height: 252px;
  width: 100%;
  z-index: 10;
  position: absolute;
  pointer-events: none;
  top: 0;
  left: 0;
  z-index: -1;
}

.background::after {
  background: url("https://imagedelivery.net/dSsqIociptNbsWng5CFX7Q/136ad597-3186-45c4-b7a1-8486e2887900/public");
  content: "";
  height: 100%;
  width: 100%;
  z-index: 10;
  position: absolute;
  opacity: 0.5;
  pointer-events: none;
  image-rendering: pixelated;
  top: 0;
  left: 0;
}

.background canvas {
  position: absolute;
  mix-blend-mode: lighten;
  z-index: 5;
}

.background img {
  max-height: 96px;
  position: absolute;
  border-radius: 4px;
  z-index: 0;
}

