@keyframes move {
  100% {
    transform: translateY(-320px);
  }
}

.background img {
  animation: move 37s infinite ease-in-out;
  animation-duration: 120s !important;

  box-shadow: 0 1px 4px var(--mono-black-a500);

  transform-origin: calc(-100vw * var(--origin-x) * 0.05)
    calc(-100vh * var(--origin-y) * 0.05);
}

.background img {
  transform: translateY(320px);
}

.background img:nth-child(2n) {
}

.background img:nth-child(1n) {
  top: calc(var(--prime-number-00) * 1%);
  left: calc(var(--prime-number-25) * 1%);

  animation-duration: calc(var(--prime-number-08) * 1s);
  animation-duration: 60s !important;
}

.background img:nth-child(2n) {
