.images {
  z-index: 0;
}

.images::after {
  content: unset;
}

.background img {
}

.images img {
  opacity: 0;
  transition: opacity 0.95s;
}

.images img:hover {
  opacity: 0.9;
}

.background {
  overflow: hidden;
  border-radius: 8px;
}

.header {
  pointer-events: none;
}

.worksuite {
}

.background img:last-child {
  transform: translateY(120px);
}

.background video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: blur(4px);
  opacity: 0.5;
}
